@use './variables-media';

@mixin phone-small-only {
  @media (max-width: variables-media.$phone-small) { @content; }
}

@mixin phone-only {
  @media (max-width: variables-media.$phone) { @content; }
}

@mixin tablet-portrait-down {
  @media (max-width: variables-media.$tablet-portrait) { @content; }
}

@mixin tablet-portrait-up {
  @media (min-width: variables-media.$tablet-portrait) { @content; }
}

@mixin tablet-landscape-down {
  @media (max-width: variables-media.$tablet-landscape) { @content; }
}

@mixin tablet-landscape-up {
  @media (min-width: variables-media.$tablet-landscape) { @content; }
}

@mixin desktop-up {
  @media (min-width: variables-media.$desktop) { @content; }
}

@mixin large-desktop-up {
  @media (min-width: variables-media.$desktop-large) { @content; }
}
